import React, {Component} from 'react';

class Resume extends Component {
  render() {
    if(this.props.data){
      var backendskillmessage = this.props.data.backendskillmessage;
      var frontendskillmessage = this.props.data.frontendskillmessage;
      var education = this.props.data.education.map(function(education){
        return <div key={education.school}><h3>{education.school}</h3>
        <p className="info">{education.degree} <span>&bull;</span><em className="date">{education.graduated}</em></p>
        <p>{education.description}</p></div>
      })
      var work = this.props.data.work.map(function(work, key){
        return <div key={key}><h3>{work.company}</h3>
            <p className="info">{work.title}<span>&bull;</span> <em className="date">{work.years}</em></p>
            <p>{work.description}</p>
        </div>
      })
      var frontendSkills = this.props.data.frontendSkills.map(function(skills){
        var className = 'bar-expand '+skills.name.toLowerCase();
        return <li key={skills.name}><span style={{width:skills.level}}className={className}></span><em>{skills.name}</em></li>
      })
      var backendSkills = this.props.data.backendSkills.map(function(skills){
        var className = 'bar-expand '+skills.name.toLowerCase();
        return <li key={skills.name}><span style={{width:skills.level}}className={className}></span><em>{skills.name}</em></li>
      })
      var awards = this.props.data.awards.map(function(award){
        return <div key={award.name}><h3>{award.name}</h3>
            <p className="info">{award.project}<span>&bull;</span>{award.company}<span>&bull;</span> <em className="date">{award.year}</em></p>
            <p>{award.description}</p>
        </div>
      })
    }

    return (
      <section id="resume">
        <div className="row education">
           <div className="three columns header-col">
              <h1><span>Education</span></h1>
           </div>

           <div className="nine columns main-col">
              <div className="row item">
                 <div className="twelve columns">
                   {education}
                 </div>
              </div>
           </div>
        </div>


        <div className="row work">

           <div className="three columns header-col">
              <h1><span>Work</span></h1>
           </div>

           <div className="nine columns main-col">
            {work}
          </div>
      </div>
        <div className="row skill">
           <div className="three columns header-col">
              <h1><span>Frontend</span></h1>
           </div>
           <div className="nine columns main-col">
              <p>{frontendskillmessage}</p>
              <div className="bars">
                 <ul className="skills">
                  {frontendSkills}
                </ul>
              </div>
          </div>
        </div>
        <div className="row skill">
           <div className="three columns header-col">
              <h1><span>Backend</span></h1>
           </div>
           <div className="nine columns main-col">
              <p>{backendskillmessage}</p>
              <div className="bars">
                 <ul className="skills">
                  {backendSkills}
                </ul>
              </div>
          </div>
        </div>
        <div className="row education">
           <div className="three columns header-col">
              <h1><span>Awards</span></h1>
           </div>

           <div className="nine columns main-col">
              <div className="row item">
                 <div className="twelve columns">
                   {awards}
                 </div>
              </div>
           </div>
        </div>
      </section>
    )
  }
}

export default Resume;
